interface SortableFieldItemProps {
  field: TemplateField;
  index: number;
  onEdit: (field: TemplateField) => void;
  onDelete: (id: string) => void;
  onDuplicate: (field: TemplateField) => void;
  onMoveUp: (index: number) => void;
  onMoveDown: (index: number) => void;
  isFirst: boolean;
  isLast: boolean;
}

const SortableFieldItem: React.FC<SortableFieldItemProps> = ({
  field,
  index,
  onEdit,
  onDelete,
  onDuplicate,
  onMoveUp,
  onMoveDown,
  isFirst,
  isLast,
}) => {
  const {
    attributes,
    listeners,
    setNodeRef,
    transform,
    transition,
    isDragging,
  } = useSortable({ id: field.id });

  const style = {
    transform: CSS.Transform.toString(transform),
    transition,
    opacity: isDragging ? 0.5 : 1,
  };

  const fieldTypeConfig = FIELD_TYPES.find((t) => t.type === field.type);

  return (
    <div ref={setNodeRef} style={style} className="mb-2">
      <Card
        size="small"
        className="hover:shadow-md transition-shadow"
        styles={{ body: { padding: '12px 16px' } }}
      >
        <div className="flex items-center justify-between">
          <div className="flex items-center gap-3 flex-1">
            <div
              {...attributes}
              {...listeners}
              className="cursor-move text-gray-400 hover:text-gray-600"
            >
              <DragOutlined style={{ fontSize: 16 }} />
            </div>

            <div className="flex items-center gap-2">
              <span className="text-xl">{fieldTypeConfig?.icon || 'üìù'}</span>
              <div>
                <div className="flex items-center gap-2">
                  <span className="font-medium">{field.label}</span>
                  {field.required && (
                    <Tag color="red" style={{ margin: 0 }}>
                      ÂøÖÂ°´
                    </Tag>
                  )}
                  {field.conditional && (
                    <Tag color="blue" style={{ margin: 0 }}>
                      Êù°‰ª∂ÊòæÁ§∫
                    </Tag>
                  )}
                </div>
                <div className="text-xs text-gray-500 mt-1">
                  {fieldTypeConfig?.label} ‚Ä¢ {field.name}
                </div>
              </div>
            </div>
          </div>

          <Space size="small">
            <Tooltip title="‰∏äÁßª">
              <Button
                type="text"
                size="small"
                icon={<ArrowUpOutlined />}
                onClick={() => onMoveUp(index)}
                disabled={isFirst}
              />
            </Tooltip>
            <Tooltip title="‰∏ãÁßª">
              <Button
                type="text"
                size="small"
                icon={<ArrowDownOutlined />}
                onClick={() => onMoveDown(index)}
                disabled={isLast}
              />
            </Tooltip>
            <Tooltip title="ÁºñËæë">
              <Button
                type="text"
                size="small"
                icon={<EditOutlined />}
                onClick={() => onEdit(field)}
              />
            </Tooltip>
            <Tooltip title="Â§çÂà∂">
              <Button
                type="text"
                size="small"
                icon={<CopyOutlined />}
                onClick={() => onDuplicate(field)}
              />
            </Tooltip>
            <Tooltip title="Âà†Èô§">
              <Button
                type="text"
                size="small"
                danger
                icon={<DeleteOutlined />}
                onClick={() => onDelete(field.id)}
              />
            </Tooltip>
          </Space>
        </div>
      </Card>
    </div>
  );
};

