                <Form.Item
                  label="Â∑•ÂÖ∑Ê†è"
                  name={['richTextConfig', 'toolbar']}
                >
                  <Select mode="multiple" placeholder="ÈÄâÊã©Â∑•ÂÖ∑Ê†èÊåâÈíÆ">
                    <Option value="bold">Á≤ó‰Ωì</Option>
                    <Option value="italic">Êñú‰Ωì</Option>
                    <Option value="underline">‰∏ãÂàíÁ∫ø</Option>
                    <Option value="link">ÈìæÊé•</Option>
                    <Option value="image">ÂõæÁâá</Option>
                    <Option value="code">‰ª£Á†Å</Option>
                    <Option value="list">ÂàóË°®</Option>
                  </Select>
                </Form.Item>
